.my-masonry-grid{display:flex;margin-left:-30px}.my-masonry-grid_column{padding-left:30px;background-clip:padding-box}.my-masonry-grid_column>.post{margin-bottom:30px}.post{position:relative;width:100%;border-radius:10px}.post-body{border-radius:10px;background-color:#F9F9F9}.post-body_header{padding:2rem 1.5rem 1rem 1.5rem;margin-top:1rem}.post-body_header .title{font-size:22px;margin-bottom:0.25rem}.post-body_header .creator,.post-body_header .time{display:block;margin-bottom:0.15rem}.post-body_header .time{font-style:italic;color:#8B8B8B}.post-body_content.youtube>a,.post-body_content.text{padding:0 1.5rem 2rem 1.5rem}.post-body_content a{text-decoration:none;transition:0.2s color ease-in}.post-body_content a:hover{color:#8B8B8B}.post-body_content p{margin-top:0.5rem}.post-body_thumbnail{margin-top:0.75rem;width:100%}.post-tags{display:flex;flex-wrap:wrap;padding:0.75rem 1rem;border-radius:0 0 10px 10px;background-color:#fff}.post-pin{opacity:.75;position:absolute;right:0;top:0;width:25px;margin-top:0.5rem;margin-right:0.5rem;cursor:pointer}.tag{opacity:75%;color:white;background-color:lightgray;padding:0.25rem 0.75rem;border-radius:25rem;margin-right:0.2rem;margin-bottom:0.4rem}.search{width:35rem;background-color:#F9F9F9;border-radius:50rem;display:flex;justify-content:space-between;padding:0.75rem 1.5rem}.search>img{width:25px;cursor:pointer}.search>input{flex-grow:1;padding:0.25rem;border:none;background-color:transparent}.search>input:focus{outline:none}.profile{display:flex;align-items:center;padding:1rem}.profile-avatar{width:5rem;height:5rem;background-color:grey;border-radius:100%;margin-right:1rem}.profile-name{font-size:36px}*{padding:0;margin:0;box-sizing:border-box;font-family:'Roboto', sans-serif;font-weight:400;font-size:15px;color:#2D2D2D}body{background-color:#fff}h1,h2,h3,h4,h5,h6{font-family:'Montserrat', sans-serif}.home-container{width:85vw;margin:auto}

